import merge from 'deepmerge';
// eslint-disable-next-line import/default
import queryString from 'query-string';
import mapJson from 'jsonpath-mapper';

import getIn from '../redux/getIn';
import {
  getSelectedFilters,
  getSearchTerm,
  getCurrentFacet,
  getSearchContext,
  selectCurrentPath,
} from '../redux/selectors';
import { removeEmptyAttributes } from '../search/util';

import { AppState } from '../models/SearchState';
import { Context } from '../models/Enums';

type SearchStateParams = {
  state: AppState;
  facet?: string;
  orderBy?: string;
  pageIndex: number;
  term?: string;
};

const searchUriTemplate = {
  path: ({ state, facet, pageIndex }: SearchStateParams) => {
    const currentFacet =
      getSearchContext(state) !== Context.listings &&
      (facet || getCurrentFacet(state));
    const currentPath = selectCurrentPath(state) || '/search';
    const newPath = currentFacet
      ? `${currentPath}/${currentFacet}`
      : currentPath;
    if (pageIndex) return `${newPath}/${pageIndex + 1}`;
    return newPath;
  },
  search: ({ state, facet, orderBy, term }: SearchStateParams) => {
    const searchContext = getSearchContext(state);
    // Lose stateFilters and currentSearch if a new
    // term is passed via an argument
    const stateFilters = term
      ? {}
      : Object.fromEntries(
          Object.entries(
            getSelectedFilters(state, facet, searchContext, 'js')
          ).map(([key, f]) => [key, f?.join(',')])
        );
    const currentSearch =
      !term && getIn(state, ['routing', 'location', 'search']);
    const currentQs = removeEmptyAttributes(queryString.parse(currentSearch));

    if (orderBy) currentQs.orderBy = orderBy;

    const searchTerm = getSearchTerm(state);
    // Use Immutable's merge to merge the stateFilters with any current Qs
    // to build the new Qs.
    const mergedSearch = removeEmptyAttributes({
      ...merge(currentQs, stateFilters),
      term: searchTerm,
    });

    return queryString.stringify(mergedSearch);
  },
  hash: {
    $path: 'state',
    $formatting: (state: AppState) =>
      getIn(state, ['routing', 'location', 'hash'], '').replace('#', ''),
  },
};

const mapStateToSearchUri = (params: SearchStateParams) =>
  mapJson(params, searchUriTemplate) as {
    path: Location['pathname'];
    search: Location['search'];
    hash: Location['hash'];
  };

export default mapStateToSearchUri;
