import {
  CLEAR_FILTERS,
  SET_ROUTE_FILTERS,
  UPDATE_PAGE_INDEX,
  UPDATE_SELECTED_FILTERS,
  UPDATE_CURRENT_FACET,
  UPDATE_CURRENT_TAB,
  UPDATE_SEARCH_TERM,
  UPDATE_SORT_ORDER,
  DO_SEARCH,
} from './types';
import { Mappers } from '../models/Search';
import {
  ClearFiltersActionCreator,
  InitListingActionCreator,
  TriggerSearchActionCreator,
} from '../models/SearchActions';

export const withMappers = <T>(action: T, mappers: Mappers) => {
  return { ...action, mappers };
};

// export const withMappers2 = (actionFunc, args, mappers) => {
//   return () => ({ ...actionFunc(args), mappers });
// };

export const triggerSearch: TriggerSearchActionCreator = ({
  config,
  context,
  debug,
  defaultLang,
  excludeIds,
  facet,
  mapper,
  params,
}) => {
  return {
    type: DO_SEARCH,
    config,
    context,
    debug,
    defaultLang,
    excludeIds,
    facet,
    mapper,
    params,
  };
};

export const initListing: InitListingActionCreator = ({
  context,
  facet,
  mapper,
  params,
}) => {
  return {
    type: SET_ROUTE_FILTERS,
    context,
    facet,
    mapper,
    params,
  };
};

export const navigate = (path: string, state?: any) => {
  return {
    type: '@ROUTING/_SET_ROUTE',
    path,
    state,
  };
};

export const clearFilters: ClearFiltersActionCreator = () => {
  return {
    type: CLEAR_FILTERS,
  };
};

export const updatePageIndex = (pageIndex: number) => {
  return {
    type: UPDATE_PAGE_INDEX,
    pageIndex,
  };
};

export const updateCurrentFacet = (facet: string) => {
  return {
    type: UPDATE_CURRENT_FACET,
    facet,
  };
};

export const updateCurrentTab = (id: number) => {
  return {
    type: UPDATE_CURRENT_TAB,
    id,
  };
};

export const updateSearchTerm = (term: string) => {
  return {
    type: UPDATE_SEARCH_TERM,
    term,
  };
};

export const updateSelectedFilters = (filter: string, key: string) => {
  return {
    type: UPDATE_SELECTED_FILTERS,
    filter,
    key,
  };
};

export const updateSortOrder = (orderBy: string, facet?: string) => {
  return {
    type: UPDATE_SORT_ORDER,
    orderBy,
    facet,
  };
};
